// import bottom from "@/components/bottom";
// 轮播插件
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import { api } from "@/utils/api.js";
import CountDown from "vue2-countdown";
import { isScrollBottom } from "@/utils/comm.js";

export default {
  name: "index",
  props: {},
  components: {
    swiper,
    swiperSlide,
    CountDown,
    isScrollBottom
  },
  data() {
    return {
      //滑动相关
      swiperOption: {
        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination"
        }
      },
      swiperOption2: {
        direction: "vertical",
        autoplay: {
          delay: 2000,
          disableOnInteraction: false
        }
        // pagination: {
        //   el: '.swiper-pagination2',
        //   clickable: true
        // }
      },
      //滑动相关end
      newHomelist: {},
      nseckilltypes: {
        temporary: {
          is_show: ""
        },
        seckill: {}
      },
      nesListName: {
        type: 1 //我自己定义的
      },
      isShowdonghua: false,
      startTime: 0,
      endTime: 0,
      currentTab: 0, //预设当前项的值
      PageNum: 1,
      goodsInfo: [],
      ifResultTop: false,
      hasScroll:false
    };
  },
  created() {
    //在渲染dom节点之前执行
    this.getHome();
    this.getActivityList();
    this.getGoods(0); //默认精品
    // var tabFixBar=document.getElementById('tabFixBar');
    // console.log('132223',tabFixBar)
    // var tabFixBar_Top=tabFixBar.offsetTop;
    // var sTop = document.body.scrollTop;
    // var result = tabFixBar_Top - sTop;
    // console.log('juli',result)
  },
  mounted() {
    //之后
    this.$nextTick(function() {
      

    var that = this;
    function addEvent(obj,type,fn){
        if(obj.attachEvent){
            obj.attachEvent('on'+type,function(){
                fn.call(obj);
            })
        }else{
            obj.addEventListener(type,fn,false);
        }
    }
    //滚动条在Y轴上的滚动距离
    function getScrollTop(){
    　　var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
    　　if(document.body){
    　　　　bodyScrollTop = document.body.scrollTop;
    　　}
    　　if(document.documentElement){
    　　　　documentScrollTop = document.documentElement.scrollTop;
    　　}
    　　scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
    　　return scrollTop;
    }
    //文档的总高度
    function getScrollHeight(){
    　　var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
    　　if(document.body){
    　　　　bodyScrollHeight = document.body.scrollHeight;
    　　}
    　　if(document.documentElement){
    　　　　documentScrollHeight = document.documentElement.scrollHeight;
    　　}
    　　scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
    　　return scrollHeight;
    }
    //浏览器视口的高度
    function getWindowHeight(){
    　　var windowHeight = 0;
    　　if(document.compatMode == "CSS1Compat"){
    　　　　windowHeight = document.documentElement.clientHeight;
    　　}else{
    　　　　windowHeight = document.body.clientHeight;
    　　}
    　　return windowHeight;
    }

    // addEvent(window,'scroll',function(){
        
    //     if(getScrollTop() + getWindowHeight() == getScrollHeight()){
    //       that.scrollBottomCB();
    // 　　}
    // });
    // addEvent(window,'scroll',function(){
    //     var sTop =document.documentElement.scrollTop || document.body.scrollTop;
    //     var tabFixBar = that.$refs.tabFixBar;
    //     var tabFixBar_Top = tabFixBar.offsetTop;

    //     if(!that.hasScroll){
    //       that.hasScroll=true;
    //       that.allHeight=tabFixBar_Top;
    //     }
    //     var allHeight=that.allHeight;
    //     if (sTop >= allHeight) {
    //       that.ifResultTop=true;
    //     } else {
    //       that.ifResultTop=false;
    //     }
    // });




    });
    
  },
  beforeDestroy(){
    window.onscroll = null;
  },
  methods: {
    //滚动到底部回调
    scrollBottomCB() {
      console.log("滚动到底部");
      var cur=this.currentTab;
      this.getGoods(cur);
    },

    countDownS_cb: function(x) {
      console.log(x);
    },
    countDownE_cb: function(x) {
      console.log(x);
    },
    async getHome() {
      let data = {
        params: {
          plat: 3,
          activity: 1
        }
      };
      const [err, res] = await api.getHomeList(data);
      if (err) {
        this.$vux.toast.text(err.msg);
        return;
      } else {
        if (res.code == 2000) {
          //console.log('aaa',res.data)
          this.newHomelist = res.data;
        }
      }
    },
    async getActivityList() {
      let data = {
        params: {
          plat: 3
        }
      };
      const [err, res] = await api.getActivityList(data);
      if (err) {
        this.$vux.toast.text(err.msg);
        return;
      } else {
        if (res.code != "2000") {
          return;
        }
        if (res.code == 2000) {
          let data = res.data;
          data.seckill.s_goods_price = parseFloat(
            data.seckill.s_goods_price
          ).toFixed(0);
          if (data.is_timing) {
            this.getLimitData();
          }
          let totalSecond = data.seckill.end_time - data.seckill.now_time;

          this.startTime = data.seckill.now_time * 1000;
          this.endTime = data.seckill.end_time * 1000;

          console.log("timetime");
          console.log(this.startTime);
          this.nseckilltypes = res.data;
          this.countdown = totalSecond;
          this.isShowdonghua = true;
        }
      }
    },
    async getLimitData() {
      let data = {
        params: {
          plat: 3
        }
      };
      const [err, res] = await api.getLimitData(data);
      if (err) {
        this.$vux.toast.text(err.msg);
        return;
      } else {
        if (res.code != "2000") {
          return;
        } else {
          res.data.seckill.s_goods_price = parseFloat(
            res.data.seckill.s_goods_price
          ).toFixed(0);
          this.nseckilltypes.seckill = res.data.seckill;
          this.nseckilltypes.activity = res.data.activity;
          // this.nseckilltypes=this.nseckilltypes;
          // this.setData({
          //   nseckilltypes: this.data.nseckilltypes,
          // });
          this.nseckilltypes = this.nseckilltypes;

          var timer = setTimeout(() => {
            this.getLimitData();
          }, 3000);
        }
      }
    },
    async getGoods(currentTab) {
      let data = {
        params: {
          plat: 3,
          page: this.PageNum,
          type: currentTab
        }
      };
      this.PageNum++;

      const [err, res] = await api.getGoods(data);
      if (err) {
        this.$vux.toast.text(err.msg);
        return;
      } else {
        if (res.code != "2000") {
          return;
        } else {
          this.goodsInfo = this.goodsInfo.concat(res.data.list);
          //this.yaoqing_list = this.yaoqing_list.concat(res.data.list);
        }
      }
    },
    swichNav(e) {
      const cur = e.target.dataset.current;
      console.log("cur", cur);
      if (this.currentTab != cur) {
        this.currentTab = cur;
        this.PageNum = 1;
        this.goodsInfo = [];
        this.getGoods(cur);
      }
    },
    link() {},
    nacigator(linketype, status){
      let url = '';
      switch (linketype) {
        case 6: 
          url = '/index/allgoods'
          break;
      }
      if(!url)return;
      this.$router.push(url);
    }
  },
  beforeDestroy(){
  }
};



